import { Grid, Typography, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, FormControlLabel, FormLabel, Radio, RadioGroup, Button, ListItem, ListItemText, List } from "@mui/material";
import React, { useEffect, useState } from "react";
import { db } from './firebaseConfig';
import { collection, addDoc, getDocs } from 'firebase/firestore';


const NAMES = [
    "DS", "KT"
];

const RADIO_OPTIONS = ["Split equally", "Owed full amount", "Tracking"];

const CURRENCIES = ["SGD", "MYR"];

interface Item {
    createdAt: Date;
    paidBy: string;
    currency: string;
    amount: number;
    description: string;
    payment: string;
}



const HomePage: React.FC = () => {
    const [data, setData] = useState<Item[]>([]);
    const [paidBy, setPaidby] = useState("");
    const [currency, setCurreny] = useState(CURRENCIES[0]);
    const [amount, setAmount] = useState<number | null>(null);
    const [description, setDescription] = useState("");
    const [payment, setPayment] = useState(RADIO_OPTIONS[0]);

    const handlePaidByChange = (event: SelectChangeEvent): void => {
        setPaidby(event.target.value as string);
    }

    const onCurrencyChange = (event: SelectChangeEvent): void => {
        setCurreny(event.target.value as string);
    }

    const onAmountChange = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>): void => {
        setAmount(Number(event.target.value));
    };

    const handleDescriptionChange = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>): void => {
        setDescription(event.target.value);
    };

    const handlePaymentChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
        setPayment((event.target as HTMLInputElement).value);
    };

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        console.log({ paidBy });
        // Add form submission logic here
        const newItem = { paidBy, currency, amount: amount ?? 0, description, payment };

        try {
            // Define the collection where you want to add the document
            const colRef = collection(db, 'payments');

            // Add a new document with a generated ID
            await addDoc(colRef, {
                ...newItem,
                createdAt: new Date(),
            });

        } catch (error) {
            console.error('Error adding document: ', error);
        }
        event.preventDefault();
    };

    useEffect(() => {
        const fetchData = async () => {
            try {
                const querySnapshot = await getDocs(collection(db, 'payments'));
                const items = querySnapshot.docs.map((doc: { id: any; data: () => any; }) => ({ id: doc.id, ...doc.data() })) as Item[];
                setData(items);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    console.log(data);


    return <Container>
        <Grid container spacing={3}>
            <Grid item xs={12} pl={2}>
                <Typography>This app is in beta</Typography>
            </Grid>
            <form onSubmit={handleSubmit}>
                <Grid item xs={12} container spacing={2} mt={2} pl={2}>
                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <InputLabel id="paidBy">Paid by</InputLabel>
                            <Select
                                value={paidBy}
                                label="Paid by"
                                onChange={handlePaidByChange}
                                required
                            >
                                {
                                    NAMES.map(name => <MenuItem key={name} value={name}>{name}</MenuItem>)
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={3} >
                        <FormControl fullWidth>
                            <InputLabel id="currency">Currency</InputLabel>
                            <Select
                                value={currency}
                                label="Currency"
                                onChange={onCurrencyChange}
                                required
                            >
                                {
                                    CURRENCIES.map(name => <MenuItem key={name} value={name}>{name}</MenuItem>)
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={9} >
                        <FormControl fullWidth>
                            <TextField
                                label="Amount"
                                type="number"
                                value={amount}
                                onChange={onAmountChange}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                required
                            />
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} >
                        <FormControl>
                            <FormLabel id="repayment">Payment</FormLabel>
                            <RadioGroup
                                value={payment}
                                name="repayment"
                                onChange={handlePaymentChange}
                            >
                                {RADIO_OPTIONS.map(option => <FormControlLabel key={option} value={option} control={<Radio />} label={option} />
                                )}
                            </RadioGroup>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} >
                        <FormControl fullWidth>
                            <TextField
                                value={description}
                                onChange={handleDescriptionChange}
                                required
                                label="Description"
                            />
                        </FormControl>
                    </Grid>
                    <Grid item>
                        <Button type="submit" variant="contained" color="primary" fullWidth>
                            Submit
                        </Button>
                    </Grid>
                </Grid>
            </form>
            <List>
                {data.map((item, index) => (
                    <ListItem key={index}>
                        <ListItemText
                            primary={`${item.amount} ${item.currency} - ${item.description}`}
                            secondary={`Paid by: ${item.paidBy} on ${item.createdAt} - Payment: ${item.payment}`}
                        />
                    </ListItem>
                ))}
            </List>        </Grid>
    </Container>
};

export default HomePage;